/* ELEMENT STYLES -------------------- */

body {
    background: #e8e7e7;
    font-family: arial;
    text-transform: uppercase;
}

header {
    color: #FFF;
    padding: 5px 10px;
    text-align: center;
    display: flex;
    align-items: center;
    background-color: #8063FA;
    border-bottom: solid 2px #D4CAFF;
}

header h1 {
    flex-grow: 1;
    font-size: 1.5em;
    letter-spacing: 2px;
    font-weight: normal;
}


/* FORMS */

form {
    display: flex;
    background-color: #8063FA;
}

input[type=text] {
    flex-grow: 1;
    border-width: 0 0 1px 0;
    margin: 15px 10px 15px 15px;
    padding: 10px;
    border-radius: 5px;
    background-color: #5B45B9;
    border-style: none;
    text-shadow: none;
    text-transform: uppercase;
    color: #C7BCF8;
    letter-spacing: 2px;
    outline: none;
}

input[type=text]::-webkit-input-placeholder {
    color: #C7BCF8;
    letter-spacing: 2px;
}

input[type=text]:focus {
    background-color: rgba(0, 0, 0, 0.2);
}

input[type=submit] {
    display: block;
    font-size: .6em;
    margin: 15px 15px 15px 0;
    padding: 10px;
    background-color: #44338D;
    border-radius: 5px;
    border: none;
    color: #fff;
    letter-spacing: 2px;
    font-weight: bold;
    text-shadow: none;
    text-transform: uppercase;
    cursor: pointer;
}

button,
input[type=submit] {
    outline: none;
    transition: background-color 0.2s ease, color 0.2s ease;
}


/* ICONS */

.is-high-score {
    fill: #FFC657;
    animation: grow 0.35s ease-in-out;
    transform-origin: 50% 50%;
}

@keyframes grow {
    50% {
        transform: scale(1.4) rotate(-3deg);
    }
}


/* COMPONENT STYLES -------------------- */

.tourlist {
    background: #fff;
    width: 90%;
    margin: 70px auto;
    box-shadow: 0 2px 0 rgb(133, 104, 250);
    border-radius: 6px;
    overflow: hidden;
}

.player,
.date {
    display: flex;
    font-size: 1.2em;
    border-bottom: solid 2px #EEEEEE;
    letter-spacing: 2px;
}

.player:last-of-type {
    border-bottom-color: #D4CAFF;
}

.tour-list:hover .remove-player {
    visibility: visible;
}

.tour-list,
.counter-score,
.date,
.seats {
    color: #2F2366;
}

.name {
    flex-grow: 1;
    line-height: 3.5em;
    padding-left: 10px;
}

.date,
.seats {
    text-align: right;
    padding-left: 0;
    line-height: 3em;
    margin-right: 0.8em;
}

.remove-player {
    font-size: 1.15em;
    border: none;
    outline: none;
    visibility: hidden;
    color: #EF5350;
    cursor: pointer;
    margin: 0 10px 0 0;
    padding: 0;
    background: none;
}

.stats,
.stopwatch {
    width: 170px;
}

.stats {
    margin: 0 0 0 28px;
    font-weight: normal;
}

.stats td:first-child {
    text-align: right;
    font-weight: normal;
    letter-spacing: 2px;
    font-size: .7em;
}

.stats td:last-child {
    text-align: left;
}

.stopwatch {
    padding: 15px 10px 5px 10px;
    margin: -5px -10px -5px 10px;
    background: #5B45B9;
}

.stopwatch-time {
    display: block;
    font-family: monospace;
    font-size: 2.15em;
}

.stopwatch button {
    margin: 8px 5px;
    background-color: #44338D;
    border-radius: 5px;
    padding: 7px 8px;
    border: none;
    color: #fff;
    letter-spacing: 2px;
    font-weight: bold;
    text-shadow: none;
    text-transform: uppercase;
}

.stopwatch button:hover,
input[type=submit]:hover {
    background: #fff;
    color: #44338D;
    cursor: pointer;
}

.stopwatch h2 {
    font-size: .6em;
    margin: 0;
    font-weight: normal;
    letter-spacing: 2px;
}

.counter {
    display: flex;
    width: 190px;
}

.counter-action {
    border: none;
    font-size: 1.15em;
    font-weight: bold;
    color: #B0AEBA;
    background-color: #DCDCDC;
    display: block;
    padding: 20px 20px;
    cursor: pointer;
    outline: none;
}

.counter-action:hover {
    color: #fff;
}

.increment:hover {
    background: #83D97C;
}

.decrement:hover {
    background: #EF5350;
}

.counter-score {
    flex-grow: 1;
    text-align: center;
    font-family: monospace;
    font-size: 2em;
    line-height: 2em;
}